.msgWrapper {
  margin-top: 25px;
}
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 18px;
}
.title {
  font-weight: 600;
  font-size: 21px;
  line-height: 26px;
  color: #000000;
}
.newMsgIcon {
  width: 24px;
  height: 24px;
  background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M19.4286 4.57143C20.3753 5.5182 20.3753 7.05323 19.4286 8L8.57143 18.8571L4 20L5.14286 15.493L16.0044 4.5758C16.8992 3.67637 18.3258 3.62787 19.2779 4.43287L19.4286 4.57143Z' stroke='%23B9B9B9' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M10.8572 20H20' stroke='%23B9B9B9' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M17.7142 7.42857L18.8571 8.57143' stroke='%23B9B9B9' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
}

.listWrapper {
  width: 100%;
}

// MessagesListItem

.listItemWrapper {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  margin-bottom: 20px;
}
.avatar {
  min-width: 53px;
  height: 53px;
  border-radius: 50%;
  overflow: hidden;
}
.placeholder {
  width: 100%;
  height: 100%;
  font-weight: 600;
  font-size: 17px;
  line-height: 21px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #FFFFFF;
  background-color: #4F31C2;
  text-align: center;
}
.indicator {
  display: inline-block;
  min-width: 10px;
  min-height: 10px;
  background-color: #4F31C2;
  border-radius: 50%;
}

.body {
  flex-grow: 1;
  width: auto;
}
.name {
  font-weight: 600;
  font-size: 15px;
  line-height: 16px;
  margin-bottom: 4px;
}
.text {
  max-height: 33px;
  max-width: 70vw;
  font-weight: 400;
  font-size: 10px;
  line-height: 11px;
  color: #808080;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.newMsgText {
  font-weight: 600;
}
